.custom-select-wrapper {
  position: relative;
  z-index: 1;
  display: block;
  user-select: none; }

.custom-select-wrapper select {
  display: none; }

.custom-select {
  position: relative; }

.custom-select-trigger {
  position: relative;
  display: block;
  padding: 16px 15px;
  margin: 0;
  line-height: 1.2;
  font-size: 1em;
  font-family: inherit;
  background-color: #fff;
  border: 1px solid #e3e7ec;
  border-radius: 2px; }

.custom-select-trigger:after {
  position: absolute;
  z-index: 1;
  display: block;
  content: '';
  width: 10px;
  height: 10px;
  top: 50%;
  right: 25px;
  margin-top: -3px;
  border-bottom: 1px solid #c1c6d0;
  border-right: 1px solid #c1c6d0;
  transform: rotate(45deg) translateY(-50%);
  transition: all 0.2s ease-in-out;
  transform-origin: 50% 0; }

.custom-select.opened .custom-select-trigger:after {
  margin-top: 3px;
  transform: rotate(-135deg) translateY(-50%); }

.custom-options {
  position: absolute;
  display: block;
  top: 100%;
  left: 0;
  right: 0;
  min-width: 100%;
  border: 1px solid #e3e7ec;
  border-radius: 2px;
  box-shadow: 0 1px 10px rgba(0, 0, 0, 0.1);
  background: #fff;
  transition: all .4s ease-in-out;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transform: translateY(10px); }

.custom-select.opened .custom-options {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
  transform: translateY(3px); }

.option-hover:before {
